div {
  box-sizing : border-box 
}
.App {
  text-align: center;
  background: #4c6075;
}
.black-nav {
  background: #4c6075;
  width: 50%;
  display: flex;
  color: #E9C358;
  border-bottom: 5px solid #E9C358;
  padding: 20px;
  margin: 0px auto 10px auto;
}
.list {
  
  background: white;
  text-align : left;
  padding : 20px;
  margin: 12px auto;
  width: 50%;
  box-shadow: 3px 3px 3px  #E9C358;
}
.list:hover {
  transform: scale(1.01);
  background-color:  #f3e4ba;
}

.modal{
  margin: 0 auto;
  padding : 20px 50px;
  background :white;
  text-align : left;
  width: 60%;
}

.remove{
  color: grey;
  text-decoration: underline;
  margin-left: 20px;
  background-color: inherit;
  border: none;
  float: right;
}
input{
  width: 40%;
  margin: 20px auto;
}
.publish {
  margin-left: 10px;
  color: white;
  background-color: #4c6075;
  border: none;
  padding: 5px;
}

.publish:hover {
  text-decoration: underline;
  transition: 0.4s;
}